import React, { useState, useEffect } from "react";
import axios from "axios";
import CryptoCurrency from "./CryptoCurrency.js";

const Ticker = () => {
  const [tickers] = useState([
    {
      id: "bitcoin",
      name: "Bitcoin",
      symbol: "BTC",
      price_usd: "1",
      percent_change_1h: "0",
      percent_change_24h: "0",
      percent_change_7d: "0"
    },
    {
      id: "ethereum",
      name: "Ethereum",
      symbol: "ETH",
      price_usd: "1",
      percent_change_1h: "0",
      percent_change_24h: "0",
      percent_change_7d: "0"
    },
    {
      id: "litecoin",
      name: "Litecoin",
      symbol: "LTC",
      price_usd: "1",
      percent_change_1h: "0",
      percent_change_24h: "0",
      percent_change_7d: "0"
    }
  ]);

  const List = tickers.map((ticker) => <CryptoCurrency data={ticker} />);
  return (
    <div className="tickers-container">
      <ul className="tickers">{List}</ul>
      <p>Information updated every minute courtesy of coinmarketcap.com</p>
    </div>
  );
};

export default Ticker;
